 

.App-logo {
    height: 25vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo infinite 20s linear;
    }
  }
  
  .App-header {
   
    min-height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo {
    0% {
      transform : translate(120px, 0%)
    }
    50% {
      transform: translate(-120px, 0%)
    }
    100%{
      transform : translate(120px, 0%)
    }
  }
  